.footer-container{
    padding: 20px 120px;
    display: flex;
    justify-content: space-between;
}
.footer-container > * {
    flex: 1;
}
.footer-dr-info img{
    border-radius: 5px;
}

.footer-dr-info p{
    line-height: 200%;
}

.footer-container li{
    line-height: 300%;
}

.footer-social-icon-container{
    display: flex;
    gap: 20px;
}

.footer-copyright-conatiner{
    text-align: center;
    padding: 5px;
}

@media (max-width: 700px) {
    .footer-container{
        display: block;
        padding: 20px;
    }
    .footer-dr-info img{
        width: 50%;
        transform: translateX(50%);
    }
}