.mob-menu{
    display: none;
}

@media (max-width:700px) {
    .desktop-menu{
        display: none;
    }
    .mob-menu{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
    }
    .mob-menu-img-container{
        display: flex;
        align-items: center;
        gap: 5px;
    }
    .mob-menu img{
        width: 24px;
        height: 24px;
    }
    .mob-menu a{
        font-size: 14px;
    }
}