.banner-carousel img,
.banner-carousel div {
  /* height: 600px; */
  width: 100%;
  object-fit: contain;
}
.slick-prev {
  left: 0;
  z-index: 10;
}
.slick-next {
  right: 50px;
}

.slick-prev::before,
.slick-next::before {
  color: #0092ff;
  font-size: 50px;
}

@media (max-width:700px) {
  .slick-prev::before,
.slick-next::before{
  display: none;
}
.banner-carousel{
  margin-bottom: 40px;
}
}