.service-container{
    padding: 20px 120px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    position: relative;
}

.service-card{
    max-width: 400px;
    /* max-height: 400px; */
    background-color: white;
    /* border: 0.5px solid rgba(0, 0, 0, 0.1); */
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    overflow: hidden;
}

.service-card:hover{
    scale: 1.09;
    transition: .5s;
    overflow: visible;
    
}

.service-card:hover p {
    display: block;
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    margin-top: -20px;
    padding: 0 20px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.service-card img{
    width: 400px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.service-detail-container{
    padding: 10px;
    background-color: white;
}

.service-detail-container p {
    font-weight: 300;
    line-height: 200%;
    display: none;
    background-color: white;
}
@media (max-width:700px){
    .service-container{
        flex-direction: row;
        padding: 20px;
        
    }
}