.about-professional-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    padding: 20px 120px;
    box-shadow: 0 2px 20px rgba(0,0,0,0.3);
}

.about-professional-container img{
    border-radius: 5px;
    width: 350px;
    aspect-ratio: 1/1;
}

.about-professional-container p{
    line-height: 200%;
}

@media (max-width:700px){
    .about-professional-container{
        display: block;
        padding: 20px;
    }
    .about-professional-img{
        display: flex;
        justify-content: center;
    }
    .about-professional-container img{
        width: 50%;
    }

}