.contact-us-container{
    padding: 50px 120px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.contact-us-first-container{
    font-size: x-large;
    /* display: flex; */
    flex-direction: column;
    align-items: end;
}

.contact-us-first-container p:nth-child(2){
    color: lightslategray;
}

.contact-us-first-container a{
    color: inherit;
}

.contact-form-container{
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 1px 5px 10px rgba(0, 0, 0, 0.146);
}

.contact-form-container input, .contact-form-container textarea{
    background-color: white;
    padding: 10px 5px;
    border: 1px solid lightgray;
    border-radius: 5px;
    min-width: 30vw;
    margin-bottom: 20px;
}

.contact-form-container label{
    margin-bottom: 20px;
}

.contact-form-container button{
    cursor: pointer;
    padding: 10px 25px;
    border: none;
    border-radius: 5px;
    box-shadow: 0 1px 5px rgba(37, 161, 255, 0.606);
}   

@media (max-width:700px){
    .contact-us-container{
        display: block;
        padding: 20px;
    }
}